const Tb = ({ val, setter, index, data, remove }) => {
   const changed = (text) => {
    let temp = data.map((each)=>{
      if(each.index === index)
      {
        return {index:each.index, value:text.target.value}
      }
      else
      {
        return each;
      }
    });
    //temp[index] = text.target.value;
    setter([...temp]);
    //console.log(data.length);
  };
  return (
    <div className="textboxes">
      <p className="delete" onClick={()=>{remove(index)}}>x</p>
      <textarea
        defaultValue={val}
        onChange={changed}
        cols="30"
        rows="10"
      ></textarea>
    </div>
  );
};
export default Tb;
