import Tb from "./Tb";
import Footer from "./Footer";
import "./CSS/Content.css";
import addlogo from "./assets/add-64.png";
import { useState } from "react";
import Popup from "./Popup";
import {v4 as uuid} from 'uuid';

const Content = () => {
  //useState
  const [Data, satData] = useState([{index:0,value:"h1"},{index:1,value:"h2"}]);
  const [popup,setPopup] = useState(false);

  let first = true;
  const [attended,setAttended] = useState(null);
  let common = attended;

  //append to array
  const addNew = () => {
    let temp = Data;
    temp.push({index:uuid(),value:""});
    satData([...temp]);
    console.log(Data);
  };


  const calcCom = () => {
    // for (let j = 0; j < Data.length; j++) {
    //   let temp = Data[j].split("\n");
    //   if (j === 0) {
    //     common = temp;
    //   } else {
    //     common = common.filter((value) => temp.includes(value));
    //   }
    // }
    // console.log(common);
    // setAttended(common);
    // setPopup(true);
    Data.forEach((each)=>{
      let temp = each.value.split("\n");
      if(first)
      {
        common = temp;
        first = false;
      } else {
        common = common.filter((value) => temp.includes(value));
      }
      setAttended(common);
      setPopup(true);
    })
  };

  const remove = (index)=>{
    let newArr = [...Data];
    newArr = newArr.filter((each)=> {return each.index!==index})
    // newArr.splice(index,1);
    console.log(newArr);
    satData(newArr);
  }

  return (
    <div className="content">
      <div className="boxcontain">

        {
          //draw TextAreas from array
          Data.map((each) => {
            return <Tb key={each.index} data={Data} val={each.value} index={each.index} setter={satData} remove={remove}/>;
          })
        }

        <div className="addNew" onClick={addNew}>
          <img src={addlogo} alt="new box" className="addlogo" />
          <p>Add TextBox</p>
        </div>

      </div>
      <Footer calculate={calcCom}/>
      <Popup display={popup} visible={setPopup} common={attended}/>
    </div>
  );
};

export default Content;
