import "./CSS/Popup.css";
import react from "./assets/react.png";

const Popup = ({display, visible, common})=>{
    if(!display) return null; 
    return(
        <div className="popup">
            {console.log(common)}
            <div className="popupcontent">
                <h1 onClick={()=>{visible(false)}}>x</h1>
                {common.length>0 && <textarea rows="20" cols="40" defaultValue={common.join('\r\n')}></textarea>}
                {common.length<=0 && <h1 className="e404">No Matches Found :(</h1>}
                <div className="anotation">
                    <p>Made with ❤️ in </p>
                    <img className="reactlogo" src={react} alt="react" />
                    <p> by Brijrajsinh Parmar </p>
                </div>

            </div>
            
        </div>
    );
}
export default Popup;